.roadmap {
    color: #fff;
    position: relative;
    padding-top: 191px;
    &__main-title {
        margin-bottom: 60px;
        font-size: 48px;
        line-height: {} }
    &__wrapper {
        position: relative;
        @include clr; }
    &__item-container {
        position: relative;
        max-height: 385px;
        height: 385px;
        @include clr; }
    &__item {
        position: relative;
        @include clr; }
    &__date {
        height: 72px;
        width: 72px;
        background-color: #09a5b0;
        color: #fff;
        position: absolute;
        top: -36px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        z-index: 200;
        &--active {
           background-color: #fff;
           color: $main-color-dark; } }
    &__month {
        display: block; }
    &__year {
        display: block; }
    &__text-container {
        position: relative;
        padding-top: 70px;
        display: flex;
        width: 50%; }
    &__icon {
        width: 48px;
        svg {
            fill: #fff;
            height: 48px;
            width: 48px; }
        @include sm {
            display: none; } }
    &__text {
        margin-left: 24px; }
    &__title {
        font-size: 36px;
        line-height: 40px;
        font-weight: 600;
        @include sm {
            font-size: 27px;
            line-height: 30px; } }
    &__descr {
        font-size: 18px;
        line-height: 2;
        @include md {
            font-size: 14px;
            line-height: 22px; } }
    &__line {
        width: 50%; } }


.roadmap__item:nth-child(1),
.roadmap__item:nth-child(3),
.roadmap__item:nth-child(5) {
    .roadmap__date {
        border-radius: 50% 50% 2px 50%; }
    .roadmap__text-container {
        float: right;
        padding-left: 36px; }
    .roadmap__text {
        margin-left: 24px; }
    .roadmap__line {
        float: left;
        img {
            display: block;
            margin-left: auto;
            margin-right: 16px; } } }

.roadmap__item:nth-child(2),
.roadmap__item:nth-child(4),
.roadmap__item:nth-child(6) {
    .roadmap__text-container {
        padding-right: 36px; }
    .roadmap__date {
        border-radius: 50% 50% 50% 2px; }
    .roadmap__line {
        float: right;
        padding-top: 18px;
        padding-left: 36px;
        img {
 } } }            // margin-left: -10px

.roadmap__item:nth-child(3),
.roadmap__item:nth-child(5) {
    .roadmap__line {
        img {
            @include rotate(180); } } }

.roadmap__item:nth-child(1), {
    .roadmap__text-container {
        padding-top: 230px; }
    .roadmap__line {
        float: left;
        img {
            margin-left: 170px; } }
    .roadmap__date {
        top: 122px; } }

