@include sm {
    .roadmap {
        &__main-title {
            margin-bottom: 131px; }
        &__item {
            margin-bottom: 46px; }
        &__item-container {
            height: auto;
            min-height: auto; }
        &__date {
            height: 52px;
            width: 52px; }
        &__text-container {
            padding-top: 0;
            width: 100%; }
        &__text {
            margin-left: 0; }
        &__date {}
        &__title {
            font-size: 27px;
            line-height: 30px; }
        &__line {
            width: 100%;
            float: none;
            position: absolute; } } }



@include sm {
    .roadmap__item:nth-child(1),
    .roadmap__item:nth-child(2),
    .roadmap__item:nth-child(5),
    .roadmap__item:nth-child(6) {
        padding-left: calc(50vw - 160px);
        .roadmap__item-container {
            float: right; }
        .roadmap__date {
            border-radius: 50% 50% 2px 50%;
            top: 0;
            left: 26px; }
        .roadmap__text-container {
            float: right;
            margin-right: 0;
            padding: 0;
            padding-left: 76px; }
        .roadmap__text {
            margin: 0; }
        .roadmap__line {
            padding: 0;
            margin: 0;
            img {
                margin: 0; } } }


    .roadmap__item:nth-child(3),
    .roadmap__item:nth-child(4) {
        padding-right: calc(50vw - 160px);
        .roadmap__item-container {
            float: left; }
        .roadmap__date {
            border-radius: 50% 50% 50% 2px;
            top: 0;
            right: -26px;
            left: auto; }
        .roadmap__text-container {
            float: left;
            margin-left: 0;
            padding: 0;
            padding-right: 75px; }
        .roadmap__text {
            margin: 0; }
        .roadmap__line {
            padding: 0; } }

    .roadmap__item:nth-child(1) {
        .roadmap__item-container {
            &:before {
                height: calc(100% + 20px);
                width: 8px;
                position: absolute;
                content: '';
                z-index: 1;
                background: #fff;
                top: 26px;
                left: 21px;
                z-index: 2; } }
        .roadmap__line {
            top: -176px;
            left: 21px; } }


    .roadmap__item:nth-child(2) {
        .roadmap__item-container {
            &:before {
                height: calc(100% - 21px);
                width: 8px;
                position: absolute;
                content: '';
                z-index: 1;
                background: radial-gradient(8px 8px, #09a5b0 0%, #09a5b0 50%, transparent 50%);
                background-repeat: repeat;
                background-size: 8px 15px;
                top: 21px;
                left: 21px;
                z-index: -1; } }
        margin-bottom: 182px;
        .roadmap__line {
            top: 100%;
            left: 21px; } }

    .roadmap__item:nth-child(3),
    .roadmap__item:nth-child(4) {
        .roadmap__item-container {
            &:before {
                height: calc(100% + 40px);
                width: 8px;
                position: absolute;
                content: '';
                z-index: 1;
                background: radial-gradient(8px 8px, #09a5b0 0%, #09a5b0 50%, transparent 50%);
                background-repeat: repeat;
                background-size: 8px 15px;
                top: 21px;
                right: 21px;
                z-index: -1; } } }

    .roadmap__item:nth-child(3) {
        .roadmap__item-container {
            &:before {
                height: calc(100% + 40px); } } }

    .roadmap__item:nth-child(4) {
        .roadmap__item-container {
            &:before {
                height: calc(100% - 30px); } } }

    .roadmap__item:nth-child(5) {
        margin-top: 190px;
        .roadmap__line {
            bottom: 100%;
            left: 21px;
            img {
                @include rotate(0); } } }

    .roadmap__item:nth-child(5),
    .roadmap__item:nth-child(6) {
        .roadmap__item-container {
            &:before {
                height: calc(100% + 46px);
                width: 8px;
                position: absolute;
                content: '';
                z-index: 1;
                background: radial-gradient(8px 8px, #09a5b0 0%, #09a5b0 50%, transparent 50%);
                background-repeat: repeat;
                background-size: 8px 15px;
                top: 21px;
                left: 21px;
                z-index: -1; } } }

    .roadmap__item:nth-child(6) {
        margin-bottom: 197px;
        .roadmap__item-container {
            &:before {
                height: calc(100% - 24px); } }
        .roadmap__line {
            top: 100%;
            left: 21px;
            img {
                @include rotate(0); } } }

    .roadmap__item:nth-child(3),
    .roadmap__item:nth-child(4) {
        .roadmap__line {
            img {
                display: none; } } } }

