.faq {
    position: relative;
    &__container-inner {
        position: relative;
        @include md {
            padding-top: 46px;
            padding-bottom: 48px; }
        @include sm {
            padding-left: 24px; } }
    &__title {
        margin-bottom: 52px;
        margin-top: -8px;
        @include md {
            margin-bottom: 30px; }
        @include sm {
            margin-bottom: 27px; } } }

.question {
    position: relative;
    padding-left: 52px;
    margin-bottom: 24px;
    @include md {
        padding-left: 66px; }
    @include sm {
        padding-left: 42px;
        padding-right: 10px; }
    &:not(:last-child) {
        margin-bottom: 28px;
        @include md {
            margin-bottom: 35px; }
        @include sm {
            margin-bottom: 24px; } }
    &__question {
        font-size: 30px;
        font-weight: 600;
        line-height: 1.6;
        color: $main-color-dark;
        @include md {
            font-size: 24px;
            line-height: 30px; }
        @include sm {
            font-size: 18px;

            line-height: 24px; } }
    &__icon {
        position: absolute;
        top: 0;
        left: 0;
        width: 36px;
        height: 36px;
        cursor: pointer;
        transform: rotate(-90deg);
        @include sm {
            width: 28px;
            top: -8px; }
        svg {
            height: 100%;
            width: 100%;
            fill: $main-color-light; } }
    &__answer {
        display: none; }
    &-active &__icon {
        transform: rotate(0); }
    &-active &__question {
        margin-bottom: 9px; }
    &-active &__answer {
        display: block;
        line-height: 2;
        font-size: 18px;
        @include md {
            margin-top: 14px;
            font-size: 14px;
            line-height: 22px; } } }

