body {
	background: $main-color-light;
	// +css-lock(16, 20, 600, 2000)
	line-height: 1.4;
	font-family: $Montserrat, sans-serif;
	font-size: 14px;
	font-weight: 400;
	// overflow-x: hidden
	@include smooth-scroll;
	@include smooth-font;
	@include prevent-text; }
* {
	box-sizing: border-box;
	// box-shadow: 0 0 0 1px #00f
	// &::-webkit-scrollbar
 } // // 	display: none;


image {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

ul {
	margin: 0;
	padding: 0; }

video {
  object-fit: cover;
  position: relative;
  object-position: 50% 50%;
  height: 100%;
  width: 100%; }


// +smooth-scroll

// ===== LAYOUT =====

.container-thin {
	max-width: 970px;
	margin: 0 auto;
	position: relative;
	@include md {
		width: auto;
		margin: 0 60px; }
	@include sm {
		margin: 0 15px; } }

.container {
	position: relative;
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
	@include md {
		padding: 0 36px; }
	@include sm {
		padding: 0 10px; } }



.white-container {
	position: relative;
	margin: 0 auto;
	max-width: 1170px;
	background-color: #fff;
	box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
	border-radius: 16px;
	// padding: 88px 100px
	padding: 0 100px;
	@include md {
		padding: 0 36px; }
	@include sm {
		padding: 32px 10px; } }

// .white-rounded-container--inner
