// 	margin: 0 auto
// 	// padding: 88px 0
// 	position: relative



// ===== BUTTONS =====
.btn {
	display: inline-block;
	border-radius: 32px;
	// height: 64px
	margin: 0 auto;
	line-height: 1;
	font-size: 18px;
	font-weight: 600;
	text-align: center;
	padding-top: 21px;
	padding-bottom: 21px;
	padding-left: 65px;
	padding-right: 65px;
	@include md {
		font-size: 16px;
		line-height: 1.15;
		padding-top: 19px;
		padding-bottom: 19px; } }


// .btn-inline
// 	display: inline-block
// 	padding-left: 65px
// 	padding-right: 65px
// 	+md
// 		padding: 0 48px

.btn-block {
	display: inline-block;
	width: 100%; }

.btn-dark {
	background-color: $main-color-dark;
	color: #fff; }

.btn-light {
	background-color: #fff;
	color: $main-color-dark; }

// .btn-sm-big
// 	+md
// 		height: 66px
// 		line-height: 66px

.btn-outline {
	border: 3px solid $main-color-dark;
	padding: 16px 65px;
	box-sizing: border-box; }

// ===== TITLES =====
.title--large {
	font-size: 60px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }


.title--xlarge {
	font-size: 72px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }

.title--thin {
	font-weight: 200; }

.out {
	overflow: hidden;
	position: relative; }
