.lang-changer__list {
  display: block;
  position: relative;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .lang-changer {
    padding: 13px 20px;
    font-size: 14px;
  }
}

.lang-changer__btn {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  padding: 10px 24px;
  background-color: #fff;
  color: #1d354e;
}

.lang-changer__btn:first-child {
  border-radius: 19px 19px 0 0;
}

.lang-changer__btn:last-child {
  border-radius: 0 0 19px 19px;
}

.lang-changer__btn--active {
  display: block;
  position: relative;
  background-color: transparent;
  color: #fff;
}

.lang-changer:hover .lang-changer__btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.lang-changer:hover .lang-changer__btn:nth-child(2) {
  top: calc((2 - 1) * 100%);
}

.lang-changer:hover .lang-changer__btn:nth-child(3) {
  top: calc((3 - 1) * 100%);
}

.lang-changer:hover .lang-changer__btn:nth-child(4) {
  top: calc((4 - 1) * 100%);
}

.lang-changer:hover .lang-changer__btn:nth-child(5) {
  top: calc((5 - 1) * 100%);
}

.lang-changer:hover .lang-changer__btn:nth-child(6) {
  top: calc((6 - 1) * 100%);
}

.lang-changer:hover .lang-changer__btn--active {
  display: block;
  position: relative;
  background-color: #ffffff;
  color: #1d354e;
}

